import { useEffect, useState } from 'react';
import { UserListApi } from '../../utils/adminApi';
import { Table } from 'antd';
const { Column } = Table;

const Income = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    UserListApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          _id: item._id.slice(0, 7),
          createdAt: item.createdAt.replaceAll(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replaceAll(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);


  return (
    <Table dataSource={tableData} rowKey="_id">
      <Column title="会员编号" dataIndex="level" />
      <Column title="资金变动" dataIndex="blance" />
      <Column title="余额" dataIndex="freeze" />
      <Column title="时间" dataIndex="createdAt" />
      <Column title="描述" dataIndex="state" />
    </Table>
  );
};

export default Income;
